import { Checkbox, Flex, Switch, Text } from "@chakra-ui/react";
import React, {lazy, FC } from "react";

interface NotificationSettingsComponentProps {
  head: string;
  body: string;
}

const NotificationSettingsComponent: FC<NotificationSettingsComponentProps> = ({
  head,
  body,
}): JSX.Element => {
  const { root, lowerSec, subHeadStyle, headingStyle, bodyStyle } = useStyles();
  return (
    <Flex {...root} flexDir="column">
      <Text {...subHeadStyle}>{head}</Text>
      <Flex {...lowerSec} flexDir={"row"} justify={"space-between"}>
        <Text {...bodyStyle} w={"45%"}>
          {body}
        </Text>
        <Checkbox borderColor={"#00000027"} spacing={"0.5rem"} colorScheme={"gray"}>
          <Text {...bodyStyle} color={"black"}>
            Email
          </Text>
        </Checkbox>
        <Checkbox borderColor={"#00000027"} spacing={"0.5rem"} colorScheme={"gray"}>
          <Text {...bodyStyle} color={"black"}>
            Push notifications
          </Text>
        </Checkbox>
        <Switch sx={{ 'span.chakra-switch__track:not([data-checked])': { backgroundColor: 'gray' }, 'span.chakra-switch__track': { backgroundColor: 'gray.900' } }} size="sm" />
      </Flex>
    </Flex>
  );
};

export default NotificationSettingsComponent;

export const useStyles = () => {
  return {
    root: {
      w: "100%",
      mb: "2%",
    },
    lowerSec: {
      w: "100%",
      align: "center",
    },
    headingStyle: {
      fontSize: ["14px", "12px", "14px", "16px", "18px"],
    },
    subHeadStyle: {
      fontSize: ["14px", "12px", "12px", "12px", "14px"],
    },
    bodyStyle: {
      fontSize: ["14px", "14px", "12px", "12px", "12px"],
      color: "#999999",
    },
  };
};
